button{
    background: transparent;
    border: none;
    margin: 5px;
    font-size: 25px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.Reset{
    width: 35px;
    height: 35px;
}

.Reset:hover{
    -webkit-transition: 0.2s;
    transition: 0.2s;
    color: red;
}

.Confirm{
    width: auto;
    height: auto;
    background: green;
    color: white;
    padding: 5px;
    text-align: center;
    border-radius: 5px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.Confirm:disabled{
    background: lightgray;
    cursor: not-allowed;
}

.Confirm:disabled:hover{
    background: gray;
}

.Confirm:hover{
    background: darkgreen;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.Cancel{
    width: auto;
    height: auto;
    font-size: 0.8em;
    background: red;
    color: white; 
    padding: 5px;
    text-align: center;
    border-radius: 5px;
}

.Cancel:hover{
    background: darkred;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.Success{
    padding: 25px;
    margin: 5px 0 45px 0;
}

.Success:hover{
    color: lawngreen;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.Success i{
    padding: 5px;
    font-size: 32px;
}

.Options:hover{
    color: lightskyblue;
}


